 .wp-block-accordion-panel-accordion-panel{
	transition: 0s !important;
 }

 .wp-block-accordion-panel-accordion-panel:is(
	.is-selected, .has-child-selected
){
	flex-grow:1;
	background-color: var(--panelBgColor);
}


 .wp-block-accordion-panel-accordion-panel::before{
	display: none !important;	
}

.wp-block-accordion-panel-accordion-panel .panel-headings{
	animation: none !important;
	transform: none !important;
}

.wp-block-accordion-panel-accordion-panel .panel-headings .panel-heading{
	color:var(--panelHeadingColorClose);
}
.wp-block-accordion-panel-accordion-panel:is(
	.is-selected, :has(.is-selected)
) .panel-headings .panel-heading{
	color:var(--panelHeadingColorOpen);
	background-color: var(--panelBgColor);
}

.wp-block-accordion-panel-accordion-panel .panel-content{
	display: none;
	flex-grow:1;
}






.wp-block-accordion-panel-accordion-panel .panel-content{
	display: none;
}
.wp-block-accordion-panel-accordion-panel:is(
	.is-selected, :has(.is-selected)
) :is(
	.panel-content, .panel
){
	width: 100%;
	transition: 0s !important;
	display: block;
	flex-grow: 1;
}
